import React from 'react';
import ImageMenu, { ImageDownload, ImageSetting } from './components/imageMenu';
import { ImagePluginConfig } from './types';

export default function menubar(config: ImagePluginConfig) {
  const { locale = {} } = config;
  return {
    menu: ImageMenu,
    download: (props) => (
      <ImageDownload config={config} {...props} />
    ),
    options: ImageSetting,
    locale,
  };
}
